import React, {useState} from 'react';
import TableComponent from '../Transport/Table'
import style from "./MainTable.module.css";
import Avatar from "@material-ui/core/Avatar";
import BookIcon from "@material-ui/icons/Book";
import Typography from "@material-ui/core/Typography";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Slider from "@material-ui/core/Slider";
import Button from "@material-ui/core/Button";
import { getBusStopInformation, getDefaultTableOptions } from '../Transport/utils';
import { defaulTableValue, useStyles } from '../Transport/Transport';


const Maintable = () => {
    const [tableOptions, setTableOptions] = useState(getDefaultTableOptions(defaulTableValue));
    const [tableValue, setTableValue] = useState();
    const classes = useStyles();

    const onSubmitForm = (e) => {
        e.preventDefault();
        const { city, selection_bus_stop, time, sort, transport_type } = tableOptions;
        getBusStopInformation(city, 'all', selection_bus_stop, time, sort, transport_type)
            .then((data) => {
                localStorage.setItem('tableValue', JSON.stringify(tableOptions));
                setTableValue(data);
            });
    };

    const onChange = ({target}) => {
        setTableOptions({...tableOptions, [target.name]: target.value});
    };

    const onRange = (event, newValue) => {
        setTableOptions({...tableOptions, time: newValue});
    };

    return (
        <div className={style.container}>
            <div className={style['main-form']}>
                <Avatar className={classes.avatar} style={{margin: '0 auto'}}>
                    <BookIcon />
                </Avatar>
                <Typography variant="h5" align='left' gutterBottom style={{textAlign: 'center'}}>
                    Информация
                </Typography>
                <form onSubmit={onSubmitForm}>
                    <div>
                        <Typography variant="h6" align='left' gutterBottom>
                            Города
                        </Typography>
                        <Select
                            value={tableOptions.city}
                            onChange={onChange}
                            name='city'
                            style={{width: '100%'}}
                        >
                            <MenuItem value='brest'>Брест</MenuItem>
                            <MenuItem value='gomel'>Гомель</MenuItem>
                            <MenuItem value='grodno'>Гродно</MenuItem>
                        </Select>
                    </div>
                    <div>
                        <Typography variant="h6" align='left' gutterBottom>
                            Тип транспорта
                        </Typography>
                        <Select
                            value={tableOptions.transport_type}
                            onChange={onChange}
                            name='transport_type'
                            style={{width: '100%'}}
                        >
                            <MenuItem value='bus'>Автобус</MenuItem>
                            <MenuItem value='trolleybus'>Троллейбус</MenuItem>
                        </Select>
                    </div>
                    <div>
                        <Typography variant="h6" align='left' gutterBottom>
                            Тип остановки
                        </Typography>
                        <Select
                            value={tableOptions.selection_bus_stop}
                            onChange={onChange}
                            name='selection_bus_stop'
                            style={{width: '100%'}}
                        >
                            <MenuItem value='dirty'>С контроллерами</MenuItem>
                            <MenuItem value='clean'>Без контроллеров</MenuItem>
                        </Select>
                    </div>
                    <div>
                        <Typography variant="h6" align='left' gutterBottom>
                            Тип сортировки
                        </Typography>
                        <Select
                            value={tableOptions.sort}
                            onChange={onChange}
                            name='sort'
                            style={{width: '100%'}}
                        >
                            <MenuItem value='Время'>Время</MenuItem>
                            <MenuItem value='Сообщения'>Сообщения</MenuItem>
                        </Select>
                    </div>
                    <div>
                        <Typography variant="h6" align='left' gutterBottom>
                            Время
                        </Typography>
                        <Slider
                            defaultValue={tableOptions.time}
                            aria-labelledby="discrete-slider"
                            valueLabelDisplay="auto"
                            step={4000}
                            marks
                            min={1000}
                            max={99999}
                            onChange={onRange}
                        />
                    </div>
                    <Button
                        type="submit"
                        variant="contained"
                        color="primary"
                        fullWidth
                        className={classes.submit}
                    >
                        Обновить
                    </Button>
                </form>
            </div>
            <div>
                <TableComponent busStopData={tableValue} />
            </div>
        </div>
    );
};

export default Maintable;