import React from 'react';
import style from './NoMatch.module.css'

const NoMatch = () => {
    return (
        <div className={style['wrap']}>
            <h1 className={style['main-text']}>Страница не найдена</h1>
            <h1 className={style['error-text']}>404</h1>
        </div>
    );
};

export default NoMatch;